<template>
    <div class="analysisOfinterestIncome">
        <a-spin tip="..." :spinning="spinning" size="large">
            <div style="textalign: center; margin-bottom: 8px">
                <a-icon type="tag" style="margin-right: 8px" />{{
                    `${dataCollection.length || 0}`
                }}
            </div>
            <a-table
                class="related-parrty-table"
                bordered
                :columns="columns"
                :data-source="dataCollection"
                :scroll="{ x: 'scollXWidth', y: 'calc(100% - 55px)' }"
                :pagination="false"
                :components="components"
            >
                <div
                    :title="record.interestRate"
                    :style="{
                        maxWidth: '200px',
                        whiteSpace: 'nowrap',
                        textOverflow: 'ellipsis',
                        overflow: 'hidden',
                        wordWrap: 'break-word',
                        wordBreak: 'break-all',
                    }"
                    slot="interestRate"
                    slot-scope="text, record"
                >
                    {{ record.interestRate }}
                </div>
            </a-table>
        </a-spin>
        <a-modal
            v-model="searchDialogVisible"
            title=""
            ok-text=""
            cancel-text=""
            @ok="onSubmitSeachItem"
            @cancel="cancelSearch"
        >
            <a-form-model
                :model="searchForm"
                :label-col="labelCol"
                :wrapper-col="wrapperCol"
            >
                <a-form-model-item label="">
                    <a-input
                        v-model="searchForm.merchantName"
                        placeholder=""
                    />
                </a-form-model-item>
                <!-- <a-form-model-item label="">
                    <a-input v-model="searchForm.customerUscc" placeholder=""/>
                </a-form-model-item> -->
            </a-form-model>
        </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import session from '../data/session/sessionUtils';
import moment from 'moment';
import { filter } from 'xe-utils/methods';
import { buildResizeableTitle } from '../plugins/vueDraggableResizable';

const columns = [
    {
        title: '',
        dataIndex: 'bankId',
        width: 170,
        ellipsis: true,
        fixed: 'left',
    },
    {
        title: '',
        dataIndex: 'bankName',
        width: 200,
        ellipsis: true,
        fixed: 'left',
    },
    {
        title: '',
        dataIndex: 'interestRate',
        width: 200,
        ellipsis: true,
        fixed: 'left',
    },
    {
        title: '1',
        dataIndex: 'month1',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '2',
        dataIndex: 'month2',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '3',
        dataIndex: 'month3',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '4',
        dataIndex: 'month4',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '5',
        dataIndex: 'month5',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '6',
        dataIndex: 'month6',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '7',
        dataIndex: 'month7',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '8',
        dataIndex: 'month8',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '9',
        dataIndex: 'month9',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '10',
        dataIndex: 'month10',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '11',
        dataIndex: 'month11',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '12',
        dataIndex: 'month12',
        width: 200,
        align: 'right',
        customRender: formatter.commafy,
    },
];

export default {
    name: 'analysisOfinterestIncome',
    data() {
        const ResizeableTitle = buildResizeableTitle(columns);
        return {
            spinning: false,
            components: {
                header: {
                    cell: ResizeableTitle,
                },
            },
            columns,
            dataCollection: [],
            selectedRowKeys: [],
            queryParams: {
                sqlId: 'DG50002',
                start: 0,
                limit: 1000,
                lockCustomerId: null,
                lockProjectId: null,
            },
            customerInformation: {},
            searchDialogVisible: false,
            searchForm: {
                merchantName: null,
                // customerUscc:null,
            },
            labelCol: { span: 6 },
            wrapperCol: { span: 13 },
        };
    },
    methods: {
        /**/
        openSearchDialog() {
            this.searchDialogVisible = true;
        },
        onSubmitSeachItem() {
            this.queryParams.start = 0;
            this.queryParams.limit = 10;
            this.queryParams.isChange = true;
            XEUtils.set(
                this.queryParams,
                'param3',
                this.searchForm.merchantName
            );
            // XEUtils.set(this.queryParams,'param4',this.searchForm.customerUscc);
            this.handleQueryRateList();
        },
        cancelSearch() {
            this.searchDialogVisible = false;
            Object.keys(this.searchForm).forEach(
                (key) => (this.searchForm[key] = null)
            );
        },
        /**/
        handleQueryRateList() {
            this.spinning = true;
            this.customerInformation = session.findUserInfo();
            this.queryParams.lockCustomerId = this.customerInformation.curCustomerId;
            this.queryParams.lockProjectId = this.customerInformation.curProjectId;
            this.$sacpApis
                .findBankList(this.queryParams)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.dataCollection = res.data;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    if (this.searchDialogVisible) this.cancelSearch();
                    this.spinning = false;
                });
        },
    },
    created() {
        this.handleQueryRateList();
    },
};
</script>


