<template>
    <div class="bankList">
        <a-spin tip="..." :spinning="spinning" size="large">
            <div style="textalign: center; margin-bottom: 8px">
                <a-icon type="tag" style="margin-right: 8px" />
                {{ `${totalCount}`
                }}{{ `  (${matchCount})` }}
            </div>
            <a-table
                :columns="columns"
                :data-source="dataList"
                bordered
                :rowKey="(row) => row.key"
                :pagination="pagination"
                :components="components"
                :scroll="{ y: 'calc(100% - 55px)', x: 'scollXWidth' }"
            >
                <template slot="customerUscc" slot-scope="text, record">
                    <editable-cell
                        :text="record.customerUscc"
                        @change="onCellChange(record, 'customerUscc', $event)"
                    />
                    <a-button
                        v-if="record.customerUscc"
                        type="primary"
                        shape="circle"
                        icon="sync"
                        @click="updateCompanyByUscc(record.customerUscc)"
                        size="small"
                        title=""
                    />

                    <a-button
                        v-if="record.customerUscc"
                        type="primary"
                        shape="circle"
                        icon="delete"
                        @click="clearUscc(record, 'customerUscc', '')"
                        size="small"
                        title=""
                    />
                </template>
                <template slot="action" slot-scope="text, record">
                    <a-popconfirm
                        title=""
                        ok-text=" "
                        cancel-text=" "
                        @confirm="deleteMatch(record)"
                    >
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="delete"
                            size="small"
                            title=""
                            class="del-match-btn"
                        />
                    </a-popconfirm>
                </template>
            </a-table>
        </a-spin>
        <a-modal
            v-model="searchDialogVisible"
            title=""
            ok-text=""
            cancel-text=""
            @ok="onSubmitSeachItem"
            @cancel="cancelSearch"
        >
            <a-form-model
                :model="searchForm"
                :label-col="labelCol"
                :wrapper-col="wrapperCol"
            >
                <a-form-model-item label="">
                    <a-input
                        v-model="searchForm.merchantName"
                        placeholder=""
                    />
                </a-form-model-item>
                <a-form-model-item label="">
                    <a-input
                        v-model="searchForm.customerUscc"
                        placeholder=""
                    />
                </a-form-model-item>
            </a-form-model>
        </a-modal>
    </div>
</template>
<script>
import { buildResizeableTitle } from '../plugins/vueDraggableResizable';
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import session from '../data/session/sessionUtils';
const EditableCell = {
    template: `
        <div class="editable-cell">
            <div v-if="editable" class="editable-cell-input-wrapper">
            <a-input :value="value" @change="handleChange" @pressEnter="check" /><a-icon
                type="check"
                class="editable-cell-icon-check"
                @click="check"
            />
            </div>
            <div v-else class="editable-cell-text-wrapper">
            {{ value || ' ' }}
            <a-icon type="edit" class="editable-cell-icon" @click="edit" />
            </div>
        </div>
        `,
    props: {
        text: String,
    },
    data() {
        return {
            value: this.text,
            editable: false,
        };
    },
    watch: {
        text: {
            handler(val, oldVal) {
                this.value = val;
            },
            immediate: true,
        },
    },
    methods: {
        handleChange(e) {
            const value = e.target.value;
            this.value = value;
        },
        check() {
            this.editable = false;
            this.$emit('change', this.value);
        },
        edit() {
            this.editable = true;
        },
    },
};
const columns = [
    {
        title: '',
        dataIndex: 'action',
        width: 80,
        fixed: 'left',
        scopedSlots: { customRender: 'action' },
    },
    {
        title: '',
        dataIndex: 'bankId',
        width: 170,
        // ellipsis: true,
        fixed: 'left',
    },
    {
        title: '',
        dataIndex: 'bankName',
        width: 300,
        // ellipsis: true,
        fixed: 'left',
    },
    {
        title: '',
        dataIndex: 'subjectName',
        width: 200,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'remain',
        width: 170,
        ellipsis: true,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '',
        dataIndex: 'debitOcc',
        width: 170,
        ellipsis: true,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '',
        dataIndex: 'creditOcc',
        width: 170,
        ellipsis: true,
        align: 'right',
        customRender: formatter.commafy,
    },
    {
        title: '',
        dataIndex: 'balance',
        ellipsis: true,
        align: 'right',
        width: 170,
        // width:'160px',
        customRender: formatter.commafy,
    },
    {
        title: '',
        dataIndex: 'customerUscc',
        width: 270,
        ellipsis: true,
        scopedSlots: { customRender: 'customerUscc' },
        customCell: (record, rowIndex) => {
            return {
                style: {
                    'background-color': '#E0F5E9',
                },
            };
        },
    },
    {
        title: '',
        dataIndex: 'customerName',
        width: 200,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'officeAddress',
        width: 200,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'registeredAddress',
        width: 200,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'baiduAddress',
        width: 200,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'areaPlace',
        width: 100,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'industrycategory',
        width: 200,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'industryclassification',
        width: 200,
        ellipsis: true,
    },
];
export default {
    components: {
        EditableCell,
    },
    name: 'bankList',
    data() {
        const ResizeableTitle = buildResizeableTitle(columns);
        return {
            components: {
                header: {
                    cell: ResizeableTitle,
                },
            },
            scollXWidth: 3000,
            value: this.text,
            editable: false,
            searchDialogVisible: false,
            spinning: false,
            columns,
            labelCol: { span: 6 },
            wrapperCol: { span: 13 },
            dataList: [],
            dataListCopy: [],
            queryParams: {
                sqlId: 'DG50002',
                start: 0,
                limit: 10,
                lockCustomerId: null,
                lockProjectId: null,
            },
            searchForm: {
                merchantName: null,
                customerUscc: null,
            },
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.queryParams.start = 0;
                    this.queryParams.limit = pageSize;
                    this.queryParams.isChange = true;
                    this.pagination.pageSize = pageSize;
                    this.queryBankListInitData();
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `${this.pagination.total}`,
                change: true,
                onChange: (current, size) => {
                    //start 0 -10 -20 -30
                    this.queryParams.start = (current - 1) * size;
                    this.queryParams.limit = this.pagination.pageSize;
                    this.queryParams.isChange = false;
                    this.pagination.current = current;
                    this.queryBankListInitData();
                },
            },
            totalCount: 0,
            matchCount: 0,
        };
    },
    methods: {
        matchBaiduAddress() {
            //
            this.spinning = true;
            this.$sacpApis
                .matchBaiduAddressByHand()
                .then((res) => {
                    if (res.success) {
                        this.queryBankListInitData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        openSearchDialog() {
            this.searchDialogVisible = true;
        },
        onCellChange(row, dataIndex, value) {
            const dataSource = [...this.dataList];
            const target = dataSource.find((item) => item.key === row.key);
            if (target[dataIndex] == value)
                return this.$message.warning('!');
            target[dataIndex] = value;
            this.spinning = true;
            if (target) {
                let params = {
                    param1: target.autoId,
                    param2: value,
                };
                this.$sacpApis
                    .matchBankByUscc(params)
                    .then((res) => {
                        const dataSource = [...this.dataListCopy];
                        const targetObj = dataSource.find(
                            (item) => item.key === row.key
                        );
                        if (res.success) {
                            const clearField = [
                                'customerName',
                                'officeAddress',
                                'registeredAddress',
                                'registerAmount',
                                'baiduAddress',
                                'areaPlace',
                                'industrycategory',
                                'industryclassification',
                            ];
                            targetObj.customerUscc = value;
                            clearField.forEach((x) => {
                                targetObj[x] = '';
                                target[x] = '';
                            });
                            this.$message.success('');
                        } else {
                            row.customerUscc = targetObj.customerUscc;
                            this.$message.error(res.resultInfo.statusText);
                        }
                    })
                    .catch((err) => this.$message.error(err))
                    .finally(() => {
                        // this.queryBankListInitData();
                        this.spinning = false;
                    });
            }
        },
        onSubmitSeachItem() {
            this.queryParams.start = 0;
            this.queryParams.limit = 10;
            this.queryParams.isChange = true;
            XEUtils.set(
                this.queryParams,
                'param3',
                this.searchForm.merchantName
            );
            XEUtils.set(
                this.queryParams,
                'param4',
                this.searchForm.customerUscc
            );
            this.queryBankListInitData();
        },
        cancelSearch() {
            this.searchDialogVisible = false;
            Object.keys(this.searchForm).forEach(
                (key) => (this.searchForm[key] = null)
            );
        },
        queryBankDataCollection() {
            //
            this.spinning = true;
            this.$sacpApis
                .matchBankList()
                .then((res) => {
                    if (res.success) {
                        if (XEUtils.has(this.queryParams, 'param3'))
                            delete this.queryParams.param3;
                        if (XEUtils.has(this.queryParams, 'param4'))
                            delete this.queryParams.param4;
                        this.queryParams.start = 0;
                        this.queryParams.limit = 10;
                        this.queryBankListInitData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                    this.isChange = false;
                });
        },
        queryBankListInitData() {
            this.spinning = true;
            let customerInformation = session.findUserInfo();
            this.dataList.length = 0;
            this.queryParams.lockCustomerId = customerInformation.curCustomerId;
            this.queryParams.lockProjectId = customerInformation.curProjectId;
            this.$sacpApis
                .findBankList(this.queryParams)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.dataList = res.data;
                        this.dataListCopy = JSON.parse(
                            JSON.stringify(res.data)
                        );
                        this.pagination.defaultPageSize = res.start;
                        this.pagination.total = res.totalCount;
                        if (this.queryParams.isChange)
                            this.pagination.current = 1; //
                        this.queryMatchNum();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    if (this.searchDialogVisible) this.cancelSearch();
                    this.spinning = false;
                });
        },
        queryMatchNum() {
            this.$sacpApis
                .matchedNum()
                .then((res) => {
                    if (res.success) {
                        this.matchCount = res.data[0].matchCount;
                        this.totalCount = res.data[0].totalCount;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        deleteMatch(record) {
            this.spinning = true;
            this.$sacpApis
                .deleteBankById({ param3: record.autoId })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.queryBankListInitData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        updateCompanyByUscc(uscc) {
            if (this.spinning) {
                return this.$message.error(',');
            }

            let par = {
                param1: uscc,
            };

            this.$confirm({
                title: ``,
                content: '',
                onOk: () => {
                    this.spinning = true;
                    this.$sacpApis
                        .updateCompanyByUscc(par)
                        .then((res) => {
                            if (res.success) {
                                this.$message.success('!');
                                this.queryBankListInitData();
                            } else {
                                this.$message.error(res.resultInfo.statusText);
                            }
                        })
                        .catch((err) => this.$message.error(err))
                        .finally(() => (this.spinning = false));
                },
                onCancel: () => {},
            });
        },
        clearUscc(record, dataIndex, value) {
            this.$confirm({
                title: ``,
                content: '',
                onOk: () => {
                    this.onCellChange(record, dataIndex, value);
                },
                onCancel: () => {},
            });
        },
    },
    created() {
        this.queryBankListInitData();
    },
};
</script>

<style lang="scss" scoped>
.editable-cell {
    display: inline-block;
    width: 180px;
}
</style>


