<template>
    <div class="billList">
        <a-spin tip="..." :spinning="spinning" size="large">
            <div style="textalign: center; margin-bottom: 8px">
                <a-icon type="tag" style="margin-right: 8px" />
                {{ `${total || 0}` }}
            </div>
            <a-table
                bordered
                :columns="columns"
                :data-source="dataCollection"
                :scroll="{ x: 'calc(1300px + 50%)', y: 'calc(100% - 55px)' }"
                :pagination="false"
                :components="components"
                :row-selection="{
                    selectedRowKeys: selectedRowKeys,
                    onChange: onSelectChange,
                }"
            >
                <span slot="subjectId" slot-scope="text, record">{{
                    `${record.subjectId}-${record.subjectName}`
                }}</span>
                <template slot="operation" slot-scope="text, record">
                    <a-button
                        type="primary"
                        shape="circle"
                        @click="handleUploadBill(record)"
                        icon="cloud-upload"
                        size="small"
                        title=""
                        class="opera-btn"
                    />
                    <a-button
                        type="primary"
                        shape="circle"
                        @click="checkConfirm(record)"
                        :icon="record.checkStatus != '1' ? 'check-square' : ''"
                        size="small"
                        :title="
                            record.checkStatus === '1' ? '' : ''
                        "
                        class="opera-btn"
                        :class="
                            record.checkStatus === '1'
                                ? 'removeBorderColor'
                                : ''
                        "
                    >
                        <img
                            src="./../assets/images/xiaoxingren.png"
                            v-if="record.checkStatus == '1'"
                            class="xxrLogo"
                        />
                    </a-button>
                    <a-button
                        type="primary"
                        shape="circle"
                        @click="handleCheckBillState(record)"
                        icon="clock-circle"
                        size="small"
                        title=""
                        class="opera-btn"
                    />
                    <a-button
                        type="primary"
                        shape="circle"
                        @click="handleCheckBillResult(record)"
                        icon="file-done"
                        size="small"
                        title=""
                        class="opera-btn"
                    />
                    <a-button
                        type="primary"
                        shape="circle"
                        @click="handleBillFiles(record)"
                        icon="eye"
                        size="small"
                        title=""
                        class="opera-btn"
                    />
                    <!-- <a-button
                        type="primary"
                        shape="circle"
                        @click="handleDownLoad(record)"
                        icon="vertical-align-bottom"
                        size="small"
                        title=""
                        class="opera-btn"
                    /> -->
                </template>
                <div
                    :title="record.interestRate"
                    :style="{
                        maxWidth: '200px',
                        whiteSpace: 'nowrap',
                        textOverflow: 'ellipsis',
                        overflow: 'hidden',
                        wordWrap: 'break-word',
                        wordBreak: 'break-all',
                    }"
                    slot="interestRate"
                    slot-scope="text, record"
                >
                    {{ record.interestRate }}
                </div>
            </a-table>
        </a-spin>
        <a-modal
            v-model="visiableUploadDialog"
            :title="uploadDialogTitle"
            on-ok="handleConfirmUpload"
            :afterClose="handleCancelUpload"
            class="upload-dialog upload-dialog1"
        >
            <template slot="footer">
                <a-button key="back" @click="handleCancelUpload">
                     
                </a-button>
                <a-button
                    key="submit"
                    type="primary"
                    @click="handleConfirmUpload"
                >
                     
                </a-button>
            </template>
            <!-- <a-input v-model="fileName" class="file-name"/> -->
            <div
                class="file-name"
                v-for="(item, index) in fileNameList"
                :key="index"
            >
                {{ item }}
            </div>
            <a-upload
                :before-upload="beforeUpload"
                multiple
                name="file"
                accept=".xls,.xlsx"
                listType="text"
            >
                <a-button type="primary">
                    <a-icon type="upload" />  
                </a-button>
            </a-upload>
        </a-modal>
        <a-modal
            v-model="visiableBillFilesDialog"
            :title="billFilesDialogTitle"
            :afterClose="handleCancelViewBillFiles"
            width="60%"
            :footer="null"
        >
            <a-spin tip="..." size="large" :spinning="deleteSpinning">
                <div style="text-align: right; margin-bottom: 10px">
                    <a-button type="primary" @click="batchDelete">
                        
                    </a-button>
                    <a-button type="primary" @click="deleteConfirm()">
                        
                    </a-button>
                </div>
                <a-table
                    :columns="columnsFiles"
                    :data-source="files"
                    :pagination="false"
                    :row-selection="{
                        selectedRowKeys: selectedBillRowKeys,
                        onChange: onSelectBillChange,
                    }"
                >
                    <span slot="action" slot-scope="text, record">
                        <a-popconfirm
                            title=""
                            ok-text=" "
                            cancel-text=" "
                            @confirm="handleDeleteFile(record)"
                        >
                            <a-button
                                type="primary"
                                shape="circle"
                                icon="delete"
                                size="small"
                                title=""
                                class="opera-btn"
                            />
                        </a-popconfirm>
                    </span>
                </a-table>
            </a-spin>
        </a-modal>
        <a-modal
            v-model="searchDialogVisible"
            title=""
            ok-text=""
            cancel-text=""
            @ok="onSubmitSeachItem"
            @cancel="cancelSearch"
        >
            <a-form-model
                :model="searchForm"
                :label-col="labelCol"
                :wrapper-col="wrapperCol"
            >
                <a-form-model-item label="">
                    <a-input
                        v-model="searchForm.merchantName"
                        placeholder=""
                    />
                </a-form-model-item>
                <a-form-model-item label="">
                    <a-input
                        v-model="searchForm.customerUscc"
                        placeholder=""
                    />
                </a-form-model-item>
            </a-form-model>
        </a-modal>
        <a-modal
            v-model="setRateDialogVisible"
            ok-text=""
            cancel-text=""
            @ok="onSubmitRate"
            @cancel="cancelSetRate"
            width="50%"
            class="set_rate_dialog"
        >
            <template slot="title">
                <div class="headerTitle">
                    <span class="a-dialog__title"></span>
                    <span
                        >[
                        {{ `${defaultRate.cfgDesc}: ${defaultRate.pbcRate}` }}
                        ]</span
                    >
                </div>
            </template>
            <a-table
                bordered
                :data-source="setRateDataSource"
                :columns="setRatecolumns"
                :scroll="{ y: 'calc(100% - 55px)' }"
                :pagination="false"
            >
                <!-- defaultRate -->
                <template slot="operation" slot-scope="text, record">
                    <a-button
                        type="primary"
                        shape="circle"
                        icon="plus-circle"
                        title=""
                        @click="handleAdd()"
                    />
                    <a-popconfirm
                        title="?"
                        @confirm="
                            () =>
                                setRateDataSource.length > 1
                                    ? onDelete(record.key)
                                    : $message.warning('1')
                        "
                    >
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="delete"
                            title=""
                        />
                    </a-popconfirm>
                </template>

                <template slot="dateRange" slot-scope="text, record, index">
                    <a-range-picker
                        format="YYYY-MM-DD"
                        @change="handleDateChange"
                        :default-value="
                            record.startDate === undefined ||
                            record.endDate === undefined
                                ? null
                                : [
                                      moment(record.startDate, dateFormat),
                                      moment(record.endDate, dateFormat),
                                  ]
                        "
                        :disabled-date="disabledRangeDate"
                        @blur="handleRangeDate(text, record, index)"
                    />
                </template>
                <template slot="rate" slot-scope="text, record, index">
                    <a-input
                        v-model="record.rate"
                        suffix="%"
                        @blur="(e) => handleChange(e.target.value, record.key)"
                    />
                </template>
            </a-table>
        </a-modal>
        <!--  -->
        <a-modal
            :visible="enterpriseMaintenanceVisible"
            ok-text=""
            cancel-text=""
            @ok="enterpriseMaintenanceOk"
            @cancel="enterpriseMaintenanceCancel"
            width="50%"
            :footer="false"
        >
       <div slot="title"
                class="identi" style="display:flex;  justify-content:space-between">
                <span></span>
                <div style="margin-right:20px">
                    <span @click="initMemberNameInfo" class="titleBtn titleBtn1">
                            
                    </span>
                        <span class="titleBtn titleBtn2" @click="importFile">
                            
                        </span>
                </div>
            </div>
            <a-table
                :columns="maintainColumns"
                :data-source="maintainData"
                :loading="maintainLoading"
                :scroll="{ y: 'calc(100vh - 260px)', x: 'scollXWidth' }"
                :pagination="false"
                :row-key="(row, idx) => `${idx}`"
                :customRow="onCell"
                :showHeader="false"
            >
                <template #tags="text, record">
                    <div style="min-height:20px; background:#CCFF80" @click="cellClick(record)">
                          <div
                            style="white-space: break-spaces"
                            v-if="!record.tagsShow">
                            <a-tag
                                color="orange"
                                style="margin: 5px"
                                v-for="(item, idx) in text"
                                :key="idx"
                            >
                                {{ item }}
                            </a-tag>
                        </div>
                           <a-select
                            v-else
                            :value="text"
                            mode="tags"
                            style="width: 100%"
                            placeholder=""
                            :dropdownMenuStyle="{ display: 'none' }"
                            @change="
                                (val) => {
                                    handleTagsChange(val, text, record);
                                }
                            "
                        >
                            <a-select-option
                                v-for="(item, idx) in text"
                                :key="`${idx}`"
                            >
                                {{ item }}
                            </a-select-option>
                        </a-select>
                    </div>
                </template>
            </a-table>
        </a-modal>
          <!--   -->
            <a-modal
            :visible="importMaintenanceVisible"
            ok-text=""
            cancel-text=""
            width="50%"
            title=''
            @cancel="importCancel"
            :footer="false"
            >
                <a-upload-dragger
                    name="file"
                    :multiple="false"
                    :before-upload="importBeforeUpload"
                    :accept="'.xlsx'"
                    @change="uploadFileChange"
                    :remove="uploadFileRemove"
                    :fileList="uploadDealFileList">
                    <p class="ant-upload-drag-icon">
                    <a-icon type="cloud-upload" />
                    </p>
                    <p class="ant-upload-hint">
                        <em style="color:#1E90FF"></em>
                    </p>
                    <p class="ant-upload-hint">
                        .xlsx
                    </p>
                </a-upload-dragger>
                <div style="width:100%;text-align:center;margin:20px 0;color:#1E90FF">
                    <div>
                    <a-radio-group name="radioGroup" v-model="importRadioValue" @change="importChange(importRadioValue)">
                        <a-radio :value="0"></a-radio>
                        <a-radio :value="1"></a-radio>
                    </a-radio-group>
                    </div>
                    <a-tooltip>
                    <a-icon @click="downloadRelated" type="vertical-align-bottom" style="color:#1E90FF;margin-top:20px"/>
                    <a @click="downloadRelated"></a>
                    </a-tooltip>
                </div>
                <div class="buttonList" style="width:100%; display:flex; justify-content:center">
                    <a-button
                        style="margin-right:20px"
                        type="primary"
                        @click="importOk"
                    ></a-button>
                    <a-button
                        type="info"
                        @click="importCancel"
                    ></a-button>
                </div>
            </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { buildResizeableTitle } from '../plugins/vueDraggableResizable';
import { formatter } from '../libs/utils/formatter';
import session from '../data/session/sessionUtils';
import moment from 'moment';
const columns = [
    {
        title: '',
        dataIndex: 'operation',
        width: 240,
        scopedSlots: { customRender: 'operation' },
    },
    {
        title: '',
        dataIndex: 'bankId',
        ellipsis: true,
        width: 140,
    },
    {
        title: '',
        dataIndex: 'bankName',
        ellipsis: true,
        width: 250,
    },
    {
        title: '',
        ellipsis: true,
        dataIndex: 'checkStatusName',
        scopedSlots: { customRender: 'checkStatusName' },
        width: 100,
    },
    {
        title: '',
        dataIndex: 'checkTime',
        ellipsis: true,
        scopedSlots: { customRender: 'checkTime' },
        width: 160,
    },
    {
        title: '',
        dataIndex: 'checkResult',
        ellipsis: true,
        scopedSlots: { customRender: 'checkResult' },
        width: 150,
    },
    {
        title: '',
        dataIndex: 'interestRate',
        width: 240,
        ellipsis: true,
    },
    {
        title: '',
        dataIndex: 'remain',
        align: 'right',
        ellipsis: true,
        customRender: formatter.commafy,
        width: 200,
    },
    {
        title: '',
        dataIndex: 'remainRate',
        ellipsis: true,
        align: 'right',
        with: 200,
    },
    {
        title: '',
        dataIndex: 'debitOcc',
        align: 'right',
        ellipsis: true,
        customRender: formatter.commafy,
        width: 200,
    },
    {
        title: '',
        dataIndex: 'debitOccRate',
        align: 'right',
        ellipsis: true,
        width: 100,
    },
    {
        title: '',
        dataIndex: 'creditOcc',
        align: 'right',
        ellipsis: true,
        customRender: formatter.commafy,
        width: 200,
    },
    {
        title: '',
        dataIndex: 'creditOccRate',
        align: 'right',
        ellipsis: true,
        width: 200,
    },
    {
        title: '',
        dataIndex: 'balance',
        align: 'right',
        ellipsis: true,
        customRender: formatter.commafy,
        width: 200,
    },
    {
        title: '',
        dataIndex: 'balanceRate',
        align: 'right',
        ellipsis: true,
        width: 200,
    },
];
export default {
    name: 'merchantList',
    data() {
        const ResizeableTitle = buildResizeableTitle(columns);
        return {
            columns,
            components: {
                header: {
                    cell: ResizeableTitle,
                },
            },
            dataCollection: [],
            spinning: false,

            queryParams: {
                sqlId: 'DG50002',
                start: 0,
                limit: 1000,
                lockCustomerId: null,
                lockProjectId: null,
            },
            /****************/
            total: 0,
            visiableUploadDialog: false,
            visiableBillFilesDialog: false,
            selectedRow: {},
            fileName: '',
            uploadDealFileList:[],
            uploadFile:[],
            importMaintenanceVisible:false,
            files: [],
            importRadioValue: 0,
            importUploadName: '',
            importUploadPath: '',
            columnsFiles: [
                {
                    title: '',
                    dataIndex: 'action',
                    width: '60px',
                    scopedSlots: { customRender: 'action' },
                },
                {
                    title: '',
                    dataIndex: 'fileIndexId',
                    ellipsis: true,
                },
                {
                    title: '',
                    dataIndex: 'fileName',
                    ellipsis: true,
                },
                {
                    title: '',
                    dataIndex: 'uploadDate',
                    ellipsis: true,
                    customRender: this.toDateString,
                },
            ],
            searchDialogVisible: false,
            searchForm: {
                merchantName: null,
                customerUscc: null,
            },
            labelCol: { span: 6 },
            wrapperCol: { span: 13 },
            selectedRowKeys: [],
            setRateDialogVisible: false,
            count: 1,
            setRateDataSource: [],
            customerInformation: {},
            startDate: null,
            endDate: null,
            rate: 0,
            endOpen: false,
            rangeDate: [],
            dateStrings: [],
            rangeDateArr: {
                autoId: '',
                rate: [],
            },
            customerInformation: {},
            setRatecolumns: [
                {
                    title: '',
                    dataIndex: 'operation',
                    width: '20%',
                    scopedSlots: { customRender: 'operation' },
                },
                {
                    title: '',
                    dataIndex: 'dateRange',
                    width: '60%',
                    scopedSlots: { customRender: 'dateRange' },
                },
                {
                    title: '',
                    dataIndex: 'rate',
                    width: '20%',
                    scopedSlots: { customRender: 'rate' },
                },
            ],
            uploadDialogTitle: '',
            billFilesDialogTitle: '',
            dateFormat: 'YYYY-MM-DD',
            checkOldItem: {},
            defaultRate: {},
            fileNameList: [],
            selectedfiles: [],
            enterpriseMaintenanceVisible: false,
            maintainColumns: [
                {
                    title: '',
                    dataIndex: 'name',
                    key: 'name',
                    width: 120,
                },
                {
                    title: '',
                    dataIndex: 'tags',
                    key: 'tags',
                    scopedSlots: { customRender: 'tags' },
                },
            ],
            maintainData: [],
            maintainLoading: {
                spinning: false,
                tip: '...',
                size: 'large',
            },
            selectedBillRowKeys: [],
            selectedBillRow: [],
            deleteSpinning: false,
        };
    },
    methods: {
        /**/
        importChange(val) {
           this.importRadioValue = val;
        },
        /**/
        importOk() {
            if (this.uploadDealFileList.length === 0) {
                this.$message.error('');
                return false;
            };
            const form = new FormData()
            form.append('file', this.uploadDealFileList[0].originFileObj)
            this.$sacpApis
                .importMemberNameTpl(form)
                .then((res) => {
                    if (res.success) {
                        this.importUploadName = res.data[0].name;
                        this.importUploadPath = res.data[0].path;
                        this.importSumbit();
                    }
                    else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                });
        },
        /**/
        importSumbit() {
            let obj =  {
                customerId: session.findUserInfo().curCustomerId,
                projectId: session.findUserInfo().curProjectId,
                menuId: localStorage.getItem('menuId') && localStorage.getItem('menuId').replaceAll(`"`, ''),
                type: this.importRadioValue,
                name: this.importUploadName,
                path: this.importUploadPath
            };
            let parmas = {
                jsonData: JSON.stringify(obj),
                customerId: session.findUserInfo().curCustomerId,
                projectId: session.findUserInfo().curProjectId,
                menuId: localStorage.getItem('menuId') && localStorage.getItem('menuId').replaceAll(`"`, '') || '',
            };
            this.$sacpApis
                .saveMemberNameTpl(parmas)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.importMaintenanceVisible = false;
                        this.queryMemberNameInfo();
                    } else {
                        this.importMaintenanceVisible = false;
                        // this.$message.error(res.resultInfo.statusText);
                        this.queryMemberNameInfo();
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                })
        },
          
        /**/
        downloadRelated() {
            let parmas = {
                customerId: session.findUserInfo().curCustomerId,
                projectId: session.findUserInfo().curProjectId,
                menuId: localStorage.getItem('menuId') && localStorage.getItem('menuId').replaceAll(`"`, ''),
            }
            this.$sacpApis
                .downloadMemberNameTpl(parmas,`.xlsx`)
                .then((res) => {
                    if (res.success) {
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                });
        },
        // importCustomRequest(file) {
        //     const form = new FormData()
        //     form.append('file', file.file)
        //     this.$sacpApis
        //         .importMemberNameTpl(form)
        //         .then((res) => {
        //             if (res.success) {
        //                 // this.uploadDealFileList = file.file.name;
        //                 this.$message.success('!');
        //             }
        //             else {
        //                 this.$message.error(res.resultInfo.statusText);
        //             }
        //         })
        //         .catch((err) => {
        //             this.$message.error(err);
        //         })
        //         .finally(() => {
        //         });
        // },
        /**/
        uploadFileChange(info) {
            let ischangeType =  info.file.type === "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
            if (!ischangeType) {
                return false;
            }
             if (ischangeType) {
                let fileList = [...info.fileList];
                fileList = fileList.slice(-1);
                this.uploadDealFileList = fileList;
            }
        },
        /**/
        uploadFileRemove() {
            this.uploadDealFileList = [];
        },
        /**/
        importBeforeUpload(file) { 
            const isUploadType =  file.type === "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";
            if (!isUploadType) {
                this.$message.error("xlsx");
                return false;
            }
            console.log(this.uploadDealFileList,99999);
            return false;
        },
        /**/
        importFile() {
            this.uploadDealFileList = [];
            this.importRadioValue = 0;
            this.importMaintenanceVisible = true;
        },
        /**/
        importCancel() {
            this.importMaintenanceVisible = false;
        },
        /**/
        onSelectChange(selectedRowKeys) {
            this.selectedRowKeys = selectedRowKeys;
            let str = '';
            selectedRowKeys.forEach((item) => {
                str += `${this.dataCollection[item].autoId},`;
            });
            let _arr = str.split(',');
            _arr.pop();
            this.rangeDateArr.autoId = _arr.join();
        } /**/,
        moment,
        disabledRangeDate(current) {
            let year = this.customerInformation.curAccYear;
            var date = new Date(year - 1, 12, '01');
            date = new Date(date.getTime() - 1000 * 60 * 60 * 24).getDate();
            return (
                (current &&
                    current <=
                        this.moment(`${year - 1}-12-${date}`).endOf('day')) ||
                current >= this.moment(`${year}-12-${date}`).endOf('day')
            );
        },
        getDateRange(startDate, endDate) {
            return startDate && endDate
                ? [
                      this.moment(startDate, 'YYYY-MM-DD'),
                      this.moment(endDate, 'YYYY-MM-DD'),
                  ]
                : null;
        },
        handleDateChange(dates, dateStrings) {
            if (dates && dateStrings) {
                // let startDate=dateStrings[0].split('-');
                // let endDate=dateStrings[1].split('-');
                // let year = this.customerInformation.curAccYear;
                this.dateStrings = dateStrings;
            }
        },
        handleRangeDate(text, record, index) {
            let obj = {
                startDate:
                    this.dateStrings && this.dateStrings.length
                        ? this.dateStrings[0]
                        : record.dateRange[0]._i,
                endDate:
                    this.dateStrings && this.dateStrings.length
                        ? this.dateStrings[1]
                        : record.dateRange[1]._i,
                interestRate: record.rate,
                key: record.key,
            };
            this.rangeDateArr.rate =
                this.rangeDateArr.rate && this.rangeDateArr.rate.length
                    ? this.rangeDateArr.rate.filter(
                          (item) => item.key != record.key
                      )
                    : [];
            this.rangeDateArr.rate.push(obj);
        },
        duringDate(startDateStr, endDateStr, curDate) {
            var curDate = new Date(curDate),
                startDate = new Date(startDateStr),
                endDate = new Date(endDateStr);
            if (curDate >= startDate && curDate <= endDate) {
                return true;
            }
            return false;
        },
        handleChange(value, key) {
            var num = new RegExp(
                /^(((\d|[1-9]\d)(\.\d{1,2})?)|100|100.0|100.00)$/
            );
            if (value == '' && value != 0) {
                // this.$message.warning('');
                this.setRateDataSource[key - 1].rate = XEUtils.commafy(0, {
                    digits: 2,
                });
                this.rangeDateArr.rate[key - 1].interestRate = 0;
            } else if (!num.test(value)) {
                this.$message.warning('');
                this.setRateDataSource[key - 1].rate = XEUtils.commafy(0, {
                    digits: 2,
                });
                this.rangeDateArr.rate[key - 1].interestRate = 0;
            } else {
                this.rangeDateArr.rate[key - 1].interestRate = value;
            }
        },
        handleAdd(value) {
            const { count, setRateDataSource } = this;
            let year = this.customerInformation.curAccYear;
            var date = new Date(year - 1, 12, '01');
            date = new Date(date.getTime() - 1000 * 60 * 60 * 24).getDate(); //
            // const newData = {
            //     key: count,
            //     dateRange:this.rangeDate = this.getDateRange(`${year}/01/01`,`${year}/12/${date}`),
            //     rate:'100',
            //     startDate:`${year}/01/01`,
            //     endDate:`${year}/12/${date}`,
            // };
            let newDateArr = [
                {
                    key: 1,
                    dateRange: (this.rangeDate = this.getDateRange(
                        `${year}-01-01`,
                        `${year}-12-${date}`
                    )),
                    rate: this.defaultRate.pbcRate,
                    startDate: `${year}-01-01`,
                    endDate: `${year}-12-${date}`,
                },
            ];
            // , 
            let index = this.selectedRowKeys[0];
            let selectedRow = this.dataCollection[index];
            let interestRateArr = selectedRow.interestRate;
            if (value === 'init') {
                if (interestRateArr && this.selectedRowKeys.length == 1) {
                    // - 
                    let rateArr = selectedRow.interestRate.split(',');
                    for (let i = 0; i < rateArr.length; i++) {
                        let _item = rateArr[i].split('(');
                        let obj = {
                            key: i + 1,
                            interestRate: _item[0],
                            startDate: _item[1].split('~')[0],
                            endDate: _item[1].split('~')[1].split(')')[0],
                        };
                        this.rangeDateArr.rate.push(obj);
                    }
                    let arr = [];
                    for (let j = 0; j < this.rangeDateArr.rate.length; j++) {
                        let rate = XEUtils.commafy(
                            this.rangeDateArr.rate[j].interestRate,
                            { digits: 2 }
                        );
                        let newObj = {
                            key: this.rangeDateArr.rate[j].key,
                            dateRange: (this.rangeDate = this.getDateRange(
                                this.rangeDateArr.rate[j].startDate,
                                this.rangeDateArr.rate[j].endDate
                            )),
                            rate: rate,
                            startDate: this.rangeDateArr.rate[j].startDate,
                            endDate: this.rangeDateArr.rate[j].endDate,
                        };
                        arr.push(newObj);
                    }
                    this.setRateDataSource = [...arr];
                } else {
                    //,
                    // this.setRateDataSource = [...setRateDataSource, newData];
                    this.setRateDataSource = [...newDateArr];
                    this.count = count + 1;
                    this.rangeDateArr.rate = newDateArr.map((item) => ({
                        key: item.key,
                        startDate: item.startDate,
                        endDate: item.endDate,
                        interestRate: XEUtils.commafy(item.rate, { digits: 2 }),
                    }));
                }
            } else {
                //[#3244] -112
                let lastItem =
                    this.rangeDateArr.rate[this.rangeDateArr.rate.length - 1];
                let maxDateDiff = XEUtils.getDateDiff(
                    lastItem.endDate,
                    `${year}-12-${date}`
                );
                if (!maxDateDiff.done)
                    return this.$message.error('!');
                let newObj = {
                    key: lastItem.key + 1,
                    dateRange: (this.rangeDate = this.getDateRange(
                        XEUtils.getWhatDay(lastItem.endDate, 1),
                        `${year}-12-${date}`
                    )),
                    rate: XEUtils.commafy(this.defaultRate.pbcRate, {
                        digits: 2,
                    }),
                    startDate: XEUtils.getWhatDay(lastItem.endDate, 1),
                    endDate: `${year}-12-${date}`,
                };
                this.setRateDataSource = [...setRateDataSource, newObj];

                this.rangeDateArr.rate.push({
                    key: lastItem.key + 1,
                    startDate: XEUtils.toDateString(
                        newObj.startDate,
                        'yyyy-MM-dd'
                    ),
                    endDate: `${year}-12-${date}`,
                    interestRate: newObj.rate,
                });
            }
        },
        onDelete(key) {
            const setRateDataSource = [...this.setRateDataSource];
            this.setRateDataSource = setRateDataSource.filter(
                (item) => item.key !== key
            );
            this.rangeDateArr.rate = this.rangeDateArr.rate.filter(
                (item) => item.key != key
            );
        },
        onCellChange(key, dataIndex, value) {
            const dataSource = [...this.setRateDataSource];
            const target = dataSource.find((item) => item.key === key);
            if (target) {
                target[dataIndex] = value;
                this.setRateDataSource = dataSource;
            }
        },
        /**/
        /**/
        handleRateByHand() {
            if (
                !this.selectedRowKeys ||
                (this.selectedRowKeys && !this.selectedRowKeys.length)
            )
                return this.$message.error('!');
            this.handleAdd('init');
            this.setRateDialogVisible = true;
        },
        onSelectChange(selectedRowKeys) {
            this.selectedRowKeys = selectedRowKeys;
            let str = '';
            selectedRowKeys.forEach((item) => {
                str += `${this.dataCollection[item].autoId},`;
            });
            let _arr = str.split(',');
            _arr.pop();
            this.rangeDateArr.autoId = _arr.join();
        },
        onSubmitRate() {
            //1. ||  return
            //2. return
            let year = this.customerInformation.curAccYear;
            let rateArr = this.rangeDateArr.rate;
            let filterDate = [];
            var date = new Date(year - 1, 12, '01');
            date = new Date(date.getTime() - 1000 * 60 * 60 * 24).getDate(); //12
            for (let i = 0; i < rateArr.length; i++) {
                // if(rateArr[i].interestRate<=0) return this.$message.error(", !");

                if (
                    filterDate.some(
                        (ele) =>
                            ele == rateArr[i].startDate ||
                            ele == rateArr[i].endDate
                    )
                ) {
                    return this.$message.error(',');
                } else {
                    filterDate.push(rateArr[i].startDate);
                    filterDate.push(rateArr[i].endDate);
                }
            }
            //
            let maxDate = XEUtils.max(
                rateArr,
                (item) => new Date(item.endDate)
            );
            let maxDateDiff = XEUtils.getDateDiff(
                maxDate.endDate,
                `${year}-12-${date}`
            );
            if (maxDateDiff.done)
                return this.$message.error(
                    `:${year}-12-${date},`
                );
            //
            let minDate = XEUtils.min(
                rateArr,
                (item) => new Date(item.startDate)
            );
            let minDateDiff = XEUtils.getDateDiff(
                `${year}-01-01`,
                minDate.startDate
            );
            if (minDateDiff.done)
                return this.$message.error(
                    `:${year}-01-01,`
                );

            // ,
            let endDateArr = XEUtils.pluck(rateArr, 'endDate');
            let fullYearDay = XEUtils.getYearDay(`${year}-12-${date}`); //
            let sum = 0;
            rateArr.forEach((item) => {
                let endMonthDate = XEUtils.getYearDay(new Date(item.endDate)); //
                let startMonthDate = XEUtils.getYearDay(
                    new Date(item.startDate)
                ); //
                let rangeDate = endMonthDate - startMonthDate + 1; //
                sum += rangeDate;
            });
            if (sum > fullYearDay) {
                return this.$message.error(',');
            } else if (sum < fullYearDay) {
                return this.$message.error(',');
            }
            let flag = this.rangeDateArr.rate.some(
                (item) => !item.interestRate
            );
            this.spinning = true;
            let obj = {
                param1: this.rangeDateArr.autoId,
                jsonData: JSON.stringify(this.rangeDateArr.rate),
            };

            this.$sacpApis
                .saveBankInterestRate(obj)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.queryBankBill();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.cancelSetRate();
                    this.spinning = false;
                });
        },
        cancelSetRate() {
            this.rangeDateArr = {
                autoId: '',
                rate: [],
            };
            this.setRateDialogVisible = false;
            this.setRateDataSource = [];
            this.selectedRowKeys = [];
            this.count = 1;
        },
        checkConfirm(value) {
            if (value.checkResult) {
                const h = this.$createElement;
                this.$confirm({
                    title: ':',
                    content: h('div', {}, [
                        h('p', ''),
                    ]),
                    okText: '',
                    okType: 'danger',
                    cancelText: '',
                    onOk: () => {
                        this.handleRateCalculator(value);
                    },
                    onCancel() {
                        // console.log('Cancel');
                    },
                });
            } else {
                this.handleRateCalculator(value);
            }
        },
        handleRateCalculator(value) {
            if (
                !value &&
                (!this.selectedRowKeys ||
                    (this.selectedRowKeys && !this.selectedRowKeys.length))
            )
                return this.$message.error('!');
            let param = value ? value.autoId : this.rangeDateArr.autoId;
            this.spinning = true;
            this.$sacpApis
                .analyzeInterestRate({ param1: param })
                .then((res) => {
                    res.success
                        ? this.queryBankBill()
                        : this.$message.error(res.resultInfo.statusText);
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                    this.rangeDateArr.autoId = '';
                    this.selectedRowKeys = [];
                });
        },
        /**/
        /**/
        openSearchDialog() {
            this.searchDialogVisible = true;
        },
        onSubmitSeachItem() {
            this.queryParams.start = 0;
            this.queryParams.limit = 10;
            this.queryParams.isChange = true;
            XEUtils.set(
                this.queryParams,
                'param3',
                this.searchForm.merchantName
            );
            XEUtils.set(
                this.queryParams,
                'param4',
                this.searchForm.customerUscc
            );
            this.queryBankBill();
        },
        cancelSearch() {
            this.searchDialogVisible = false;
            Object.keys(this.searchForm).forEach(
                (key) => (this.searchForm[key] = null)
            );
        },
        /**/
        handleUploadBill(record) {
            //
            this.visiableUploadDialog = true;
            this.uploadDialogTitle = ` - ${record.bankName}`;
            this.selectedRow = record;
        },
        handleCancelUpload() {
            this.visiableUploadDialog = false;
            this.fileName = null;
            this.uploadFile = null;
            this.fileNameList = [];
        },
        beforeUpload(file, fileList) {
            this.uploadFile = [];
            this.fileNameList = [];
            if (!fileList.length) return;
            for (let i = 0; i < fileList.length; i++) {
                let fileType = fileList[i].name.split('.');
                const fileFormat = fileType[fileType.length - 1];
                // console.log('fileFormat',fileFormat)
                if (fileFormat != 'xlsx' && fileFormat != 'xls') {
                    this.fileName = null;
                    this.uploadFile = null;
                } else {
                    this.fileNameList.push(fileList[i].name);
                }
            }
            this.uploadFile = fileList;
        },
        handleConfirmUpload() {
            if (this.uploadFile && !this.uploadFile.length) return;
            for (let i = 0; i < this.uploadFile.length; i++) {
                let fileType = this.uploadFile[i].name.split('.');
                const fileFormat = fileType[fileType.length - 1];
                if (fileFormat != 'xlsx' && fileFormat != 'xls') {
                    return this.$message.error('"excel!"');
                }
            }
            let formData = new FormData();
            // formData.append('file',this.uploadFile);
            this.uploadFile.forEach((el, idx) => {
                formData.append(['file' + (idx + 1)], el);
            });
            formData.append(`param1`, this.selectedRow.autoId);
            this.spinning = true;
            this.visiableUploadDialog = false;
            this.$sacpApis
                .uploadBankBill(formData)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.handleCancelUpload();
                        this.queryBankBill();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        handleCheckBillState(record) {
            //
            if (
                !record &&
                (!this.selectedRowKeys ||
                    (this.selectedRowKeys && !this.selectedRowKeys.length))
            )
                return this.$message.error('!');
            let param = record ? record.autoId : this.rangeDateArr.autoId;
            this.spinning = true;
            this.$sacpApis
                .getCheckBankFileStatus({ param1: param })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('');
                        this.queryBankBill();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        handleCheckBillResult(record) {
            //
            if (!record.checkFileName)
                return this.$message.error('!');
            var nodeData = {
                extraOptions: record,
                currentNode: {
                    extraOptions: record,
                },
            };
            nodeData.autoId = nodeData.extraOptions.autoId;
            sessionStorage.setItem(
                'checkBankResultsNode',
                JSON.stringify(nodeData)
            );
            window.open(
                '/Faith/dgcenter.do?m=checkBankResults&projectId=' +
                    nodeData.extraOptions.projectId
            );
        },
        handleBillFiles(record) {
            //
            this.spinning = true;
            this.checkOldItem = record;
            let obj = {
                param1: session.findUserInfo().curCustomerId,
                param2: session.findUserInfo().curProjectId,
                start: 0,
                limit: 1000,
                sqlId: 'DG50006',
                param3: record.autoId,
            };
            this.$sacpApis
                .findBankList(obj)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.files = res.data || [];
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.billFilesDialogTitle = `-${record.bankName}`;
                    this.visiableBillFilesDialog = true;
                    this.spinning = false;
                    this.selectedBillRow = [];
                    this.selectedBillRowKeys = [];
                });
        },
        handleDownLoad(record) {
            if (!record.checkFileFullPath)
                return this.$message.error(
                    ''
                );
            this.$sacpApis.downloadCheckResultFile(
                { param1: record.autoId },
                `${record.bankId}-${record.bankName}.xlsx`
            );
        },
        handleDeleteFile(record) {
            //
            this.deleteSpinning = true;
            this.$sacpApis
                .deleteBankDgFile({ param1: record.autoId })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.deleteSpinning = false;
                        this.handleBillFiles(this.checkOldItem);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.deleteSpinning = false));
        },
        handleCancelViewBillFiles() {},
        queryBankBill() {
            this.spinning = true;
            let customerInformation = session.findUserInfo();
            this.queryParams.lockCustomerId = customerInformation.curCustomerId;
            this.queryParams.lockProjectId = customerInformation.curProjectId;
            this.$sacpApis
                .queryBankBill(this.queryParams)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.dataCollection = res.data;
                        this.total = res.totalCount;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    if (this.searchDialogVisible) this.cancelSearch();
                    this.spinning = false;
                    this.isChange = false;
                    this.getPbcRate();
                });
        },
        toDateString(value, row, index) {
            return XEUtils.toDateString(value, 'yyyy-MM-dd HH:mm:ss');
        },
        getPbcRate() {
            this.$sacpApis
                .getPbcRate()
                .then((res) => {
                    if (res.success) {
                        this.defaultRate = res.data[0];
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err));
        },
        enterpriseMaintenanceOk() {
            this.enterpriseMaintenanceVisible = false;
        },
        shownEtrpriseMaintenanceDialog() {
            this.enterpriseMaintenanceVisible = true;
            this.queryMemberNameInfo();
        },
        enterpriseMaintenanceCancel() {
            this.enterpriseMaintenanceVisible = false;
        },
        handleTagsChange(value, text, row) {
            const union = [];
            if (value.length > text.length) {
                value.forEach((x) => {
                    if (text.indexOf(x) == -1) {
                        union.push(x);
                    }
                });
                text.push(union[0]);
                this.saveMemberNameInfo(union[0], row);
            } else {
                text.forEach((x) => {
                    if (value.indexOf(x) == -1) {
                        union.push(x);
                    }
                });
                const idx = text.indexOf(union[0]);
                text.splice(idx, 1);
                this.deleteMemberNameInfo(union[0], row);
            }
        },
        cellClick(row) {
            row.tagsShow = true;
        },
        onCell(record, index) {
            return {
                on: {
                    // 
                    click: (event) => {}, // 
                    dblclick: (event) => {},
                    contextmenu: (event) => {},
                    mouseenter: (event) => {
                        record.tagsShow = true;
                    }, // 
                    mouseleave: (event) => {
                        if (!record.tagsShow) return;
                        if (record.tagsShow) {
                            record.tagsShow = false;
                            // this.$confirm({
                            //     title: '',
                            //     content: '',
                            //     onOk: () => {
                            //         this.updateCashFlowRules(record);
                            //     },
                            //     onCancel() {
                            //         // console.log('Cancel');
                            //     },
                            // });
                        }
                    },
                },
            };
        },
        // 
        initMemberNameInfo() {
            this.maintainLoading.spinning = true;
            this.maintainLoading.tip = '...';
            this.$sacpApis
                .initMemberNameInfo()
                .then((res) => {
                    if (res.success) {
                        this.maintainLoading.spinning = false;
                        this.$message.success('!');
                        if (res.data && res.data[0]) {
                            this.hanldeTagsData(res.data[0]);
                        }
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.maintainLoading.spinning = false;
                });
        },
        // 
        queryMemberNameInfo() {
            this.maintainLoading.spinning = true;
            this.maintainLoading.tip = '...';
            this.$sacpApis
                .queryMemberNameInfo()
                .then((res) => {
                    if (res.success) {
                        this.maintainLoading.spinning = false;
                        if (res.data && res.data[0]) {
                            this.hanldeTagsData(res.data[0]);
                        }
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.maintainLoading.spinning = false;
                });
        },
        // 
        saveMemberNameInfo(param, row) {
            this.maintainLoading.spinning = true;
            this.maintainLoading.tip = '...';
            this.$sacpApis
                .saveMemberNameInfo({ param1: row.name, param2: param })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.queryMemberNameInfo();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.maintainLoading.spinning = false;
                });
        },
        // 
        deleteMemberNameInfo(param, row) {
            this.maintainLoading.spinning = true;
            this.maintainLoading.tip = '...';
            this.$sacpApis
                .deleteMemberNameInfo({ param1: row.name, param2: param })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.queryMemberNameInfo();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.maintainLoading.spinning = false;
                });
        },
        hanldeTagsData(data) {
            const maintainData = [];
            for (const key in data) {
                maintainData.push({
                    name: key,
                    tags: data[key] ? data[key].split('|') : [],
                    tagsShow: false,
                });
            }
            this.maintainData = maintainData;
        },
        deleteConfirm(autoId) {
            this.$confirm({
                title: ':',
                content: '',
                okText: '',
                okType: 'danger',
                cancelText: '',
                onOk: () => {
                    this.deleteAllBankDgFile(autoId);
                },
                onCancel: () => {},
            });
        },
        // 
        deleteAllBankDgFile(autoId) {
            this.deleteSpinning = true;
            this.$sacpApis
                .deleteAllBankDgFile({
                    param1: this.checkOldItem.autoId,
                    param2: autoId,
                })
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.handleBillFiles(this.checkOldItem);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.deleteSpinning = false;
                });
        },
        onSelectBillChange(selectedRowKeys, selectedRow) {
            this.selectedBillRowKeys = selectedRowKeys;
            this.selectedBillRow = selectedRow;
        },
        batchDelete() {
            const autoId = this.selectedBillRow.map((x) => x.autoId).join(',');
            if(!autoId.length)return this.$message.error('!');
            this.deleteConfirm(autoId);
        },
    },
    created() {
        this.customerInformation = session.findUserInfo();
        this.queryBankBill();
    },
};
</script>

<style lang="scss" scoped>
/deep/.upload-dialog1 {
    .ant-modal-body {
        display: block;
        div {
            line-height: 30px;
        }
    }
}
.titleBtn1 {
    margin-right:20px;
    background-color:royalblue;
    border-radius:25px;
    padding:5px 10px;
    cursor: pointer;
}
.titleBtn2 {
    background-color:royalblue;
    border-radius:25px;
    padding: 5px 10px;
    cursor: pointer;
}
.titleBtn:hover {
    background-color:royalblue;
}
</style>


