<template>
    <a-spin tip="..." :spinning='spinning' size='large' class="bank-range-spin">
        <div class="bankRangeSelection">
            <div style="textAlign:center;margin-bottom:8px">
                <a-icon type="tag" style="margin-right:8px;"/>
                {{`${dataList.length || 0}`}}
            </div>
            <a-table
                :columns="columns"
                :data-source="dataList"
                bordered
                :pagination='false'
                :rowKey="row=>row.key"
                :scroll="{ y:'calc(100% - 72px)'}"
                :components="components"
                class="bank-range-table"
            >
                <template slot='subjectIsMerchant'  slot-scope="text, record">
                    <a-checkbox @change="onChange('subjectName',record)" :checked="record.subjectIsMerchant == '1'">
                        
                    </a-checkbox>
                </template>
                <template slot='assitemIsMerchant'  slot-scope="text, record">
                    <a-checkbox @change="onChange('account',record)" :checked="record.assitemIsMerchant == '1'">
                        
                    </a-checkbox>
                </template>
                <template slot='assItemName'  slot-scope="text, record">
                    <span @click="openSelectDialog(record)" class="select-assItemName">
                        {{ record.assItemName }}
                    </span>
                </template>
                <template slot='action'  slot-scope="text, record">
                    <a-popconfirm
                        title="?"
                        ok-text=""
                        cancel-text=""
                        @confirm="deleteRow(record)"
                    >
                    <a ></a>
                    </a-popconfirm>
                </template>
            </a-table>
            <!--  -->
            <a-modal
                v-model="addDataDialogVisible"
                class="assSubjectDialog"
                title=""
                ok-text=""
                cancel-text=""
                width="50%"
                @ok="submitAddData"
                @cancel="hideModal"
            >
                <a-tree
                    v-model="checkedKeys"
                    checkable
                    :expanded-keys="expandedKeys"
                    :selected-keys="selectedKeys"
                    :tree-data="subjectTree"
                    :checkStrictly="checkStrictly"
                    :replace-fields="{}"
                    @expand="onExpand"
                />
            </a-modal>
            <!--  -->
            <a-modal
                v-model="selectDialogVisible"
                class="assSubjectDialog"
                title=""
                ok-text=""
                cancel-text=""
                width="50%"
                @ok="submitassitemIsBankData"
                @cancel="hideassitemIsBankModal"
            >
                <a-tree
                    v-model="assitemIsBankCheckedKeys"
                    checkable
                    :tree-data="assitemIsBankTree"
                    :expanded-keys="assitemIsBankExpandedKeys"
                    :selected-keys="assitemIsBankSelectedKeys"
                    :checkStrictly="checkStrictly"
                    @expand="assitemIsBankOnExpand"
                />
                <!--  :load-data="onLoadData" -->
                <a-empty :image="simpleImage"  v-if="!this.assitemIsBankTree || !this.assitemIsBankTree.length"/>
            </a-modal>
        </div>
    </a-spin>
</template>
<script>
import { buildResizeableTitle } from '../plugins/vueDraggableResizable';
import session from '../data/session/sessionUtils';
import XEUtils from 'xe-utils';
import { Empty } from 'ant-design-vue';
const renderContent = (value, row, index) => {
    const obj = {
        children: value,
        attrs: {},
    };
    return obj;
    };

export default {
    name:'bankRangeSelection',
    data(){
        const columns = [
            {
                title: '',
                dataIndex: 'subjectName',
                width:170,
                customRender: renderContent
            },
            {
                title: '',
                colSpan: 2,
                width:170,
                key: 'subjectIsMerchant',
                scopedSlots: { customRender: 'subjectIsMerchant' },
            },
            {
                title: '',
                colSpan: 0,
                width:200,
                dataIndex: 'assitemIsMerchant',
                key: 'assitemIsMerchant',
                scopedSlots: { customRender: 'assitemIsMerchant' },
            },
            {
                title: '',
                dataIndex: 'assItemName',
                key: 'assItemName',
                scopedSlots: { customRender: 'assItemName' },
            },
            {
                title: '',
                key: 'action',
                width:170,
                scopedSlots: { customRender: 'action' },
            },
        ];
        const ResizeableTitle = buildResizeableTitle(columns);
        return{
             components: {
                header: {
                    cell: ResizeableTitle,
                },
            },
            columns,
            dataList:[],
            disabledSelect:false,
            key:-1,
            customerInformation:session.findUserInfo(),
            addDataDialogVisible:false,
            subjectTree:session.findBankRangeTree(),
            checkedKeys:[],
            expandedKeys:[],
            selectedKeys:[],
            spinning:false,
            assitemTypeTree:[],
            selectDialogVisible:false,
            assitemIsBankExpandedKeys:[],
            assitemIsBankSelectedKeys:[],
            assitemIsBankTree:[],
            assitemIsBankCheckedKeys:[],
            currentRow:{},
            checkStrictly:true,
            saveSuccess:false,
        }
    },
    methods:{
        openSubjectTreeDialog(){
            this.addDataDialogVisible=true;
        },
        submitAddData(){ //
            if(this.checkedKeys.length == 0) return this.$message.warning("");
            let toTreeArray=XEUtils.toTreeArray(this.subjectTree);
            let arr=[];
            this.checkedKeys.checked.map(val=>{
                let  selectedTree=toTreeArray.filter(item=>item.subjectId == val)[0];
                delete selectedTree['children'];
                this.dataList.push(selectedTree);
            });
            this.checkedKeys=[];
            this.hideModal();
        },
        onExpand(expandedKeys) {
            this.expandedKeys = expandedKeys;
        },
        onCheck(checkedKeys) {
            this.checkedKeys = checkedKeys;
        },
        hideModal(){ //
            this.addDataDialogVisible=false;
        },
        openSelectDialog(row){  // - 
            if(!row.assitemIsMerchant || (row.assitemIsMerchant && row.assitemIsMerchant == "0")) return this.$message.warning("!");
            this.selectDialogVisible=true;
            this.currentRow=row;
            let params={
                lockCustomerId:this.customerInformation.curCustomerId,
                lockYyyy:this.customerInformation.curAccYear,
                param11:row.subjectId,
                param20:1,
            };
            this.queryBankTree(params);
        },
        onLoadData(treeNode){  //
            // console.log(treeNode)
            return new Promise((resolve)=>{
                let params={
                    lockCustomerId:this.customerInformation.curCustomerId,
                    lockYyyy:this.customerInformation.curAccYear,
                    param11:this.currentRow.subjectId,
                    param21:treeNode.dataRef.assItemId,
                };
                this.queryBankTree(params);
                resolve();
            })
        },
        queryBankTree(params){
            this.$sacpApis.findAssitemTypeTree(params).then(
                res=>{
                    if(res.success){
                        res.data.map(item=>{
                            item.isLeaf= (item.parentId === '' && !item.isLeaf) || !item.isLeaf ?false :true;
                        });
                        if(params.param21){
                            let splitassitemIsBankTree=this.assitemIsBankTree.filter(item=>item.id === params.param21);
                            splitassitemIsBankTree.push(...res.data);
                            XEUtils.toArrayTree(splitassitemIsBankTree,{sortKey:'id'});  //id
                            splitassitemIsBankTree=splitassitemIsBankTree.filter(item=>item.parentId === ""); //
                            this.assitemIsBankTree=this.assitemIsBankTree.filter(item=>item.id != splitassitemIsBankTree[0].id );

                            this.assitemIsBankTree.push(splitassitemIsBankTree[0]);
                            this.assitemIsBankTree=XEUtils.sortBy(this.assitemIsBankTree,'id');
                        }else{
                            this.assitemIsBankTree=res.data;
                        }
                    }else{
                        this.$message.error(res.resultInfo.statusText);
                    }
                }
            ).catch(err=>this.$message.error(err)).finally(
                ()=>{
                    this.spinning=false;
                    // this.assitemIsBankExpandedKeys=[];
                }
            );
        },
        submitassitemIsBankData(){ //
            if(this.assitemIsBankTree.length == 0 || (!this.assitemIsBankCheckedKeys || this.assitemIsBankCheckedKeys.length == 0)) return this.hideassitemIsBankModal()
            let toTreeArray=XEUtils.toTreeArray(this.assitemIsBankTree);
            let selectedArr=[];
            this.assitemIsBankCheckedKeys.checked.map(val =>{
                selectedArr.push(toTreeArray.filter(item=>item.assItemId == val)[0])
            });
            let assItemId,assItemName;
            (selectedArr && selectedArr.length) && selectedArr.map(item=>{
                if(item){
                    assItemId+=`,${item.assItemId}`;
                    assItemName+=`,${item.assItemName}`;
                }
            })
            assItemId=assItemId.split(',').filter(item=>item != 'undefined').join();
            assItemName=assItemName.split(',').filter(item=>item != 'undefined').join();
            this.currentRow.assItemId=assItemId;
            this.currentRow.assItemName=assItemName;
            this.hideassitemIsBankModal();
        },
        hideassitemIsBankModal(){
            this.selectDialogVisible=false;
            this.assitemIsBankCheckedKeys=[];
            this.assitemIsBankTree=[];
            this.assitemIsBankExpandedKeys=[];
        },
        assitemIsBankOnExpand(expandedKeys,{expanded: bool, node}){
            this.assitemIsBankExpandedKeys = expandedKeys;
            let params={
                    lockCustomerId:this.customerInformation.curCustomerId,
                    lockYyyy:this.customerInformation.curAccYear,
                    param11:this.currentRow.subjectId,
                    param21:this.assitemIsBankExpandedKeys[this.assitemIsBankExpandedKeys.length-1],
            };
            if(expandedKeys && expandedKeys.length) {
                this.queryBankTree(params);
            }
        },
        onChange(value,row){ //checkbox
            this.key=row.key
            if(value == 'subjectName'){
                if(!row.subjectIsMerchant || row.subjectIsMerchant == '0'){
                    row.subjectIsMerchant="1";
                }else{
                    row.subjectIsMerchant="0";
                }
            }else{
                if(!row.assitemIsMerchant  || row.assitemIsMerchant == "0"){
                    row.assitemIsMerchant="1";
                }else{
                    row.assitemIsMerchant="0";
                }
            }
            //key
            row.key=`${row.key}+a`;
        },
        submitBankRangeData(){  //  1.; 2.,
            let selectMerchantFlag=this.dataList.some(item=>item.subjectIsMerchant == '0' && item.assitemIsMerchant== '0');
            if(selectMerchantFlag) return this.$message.error("");
            let assitemIsMeerchantFlag=this.dataList.some(item=>item.assitemIsMerchant == '1' && !item.assItemName);
            if(assitemIsMeerchantFlag) return this.$message.error("");
            this.spinning=true;
            this.$sacpApis.saveBankRange(this.dataList).then(
                res=>{
                    if(res.success) {
                        this.$store.commit('setNextStep',true);
                        this.$message.success("!");
                    }else{
                        this.$message.error(res.resultInfo.statusText);
                    }
                }
            ).catch(
                err=>{
                    this.$message.error(err)
                })
            .finally(
                ()=>{
                    this.spinning=false;
                    this.hideassitemIsBankModal();
                    this.queryBankRangeData();
                    // this.$store.commit('setSaveMerchantRange',false);
                }
            );
        },
        deleteRow(row){ //
            this.dataList=this.dataList.filter(item=>item.key != row.key);
        },
        queryBankRangeData(){
            this.spinning=true;
            this.$sacpApis.queryBankRange().then(
                res=>{
                    if(res.success){
                        if(res.data && res.data.length) {
                            res.data.forEach((item,index)=>{
                                XEUtils.set(item,'key',index);
                            })
                        };
                        this.dataList = res.data;
                    }else{
                        this.$message.error(res.resultInfo.statusText);
                    }
                }
            ).catch(err=>{
                this.$message.error(err);
            }).finally(()=>{
                this.spinning=false;
            })
        },
        resetBank(){
            this.spinning=true;
            this.$sacpApis.resetBank().then(
                res=>{
                    res.success ?  this.queryBankRangeData() && this.$message.success('!') :this.$message.error(res.resultInfo.statusText);
                }
            ).catch(err=>{
                this.$message.error(err);
            }).finally(
                ()=>{
                    this.spinning=false;
                }
            )
        }
    },
    created(){
        this.queryBankRangeData();
    },
    beforeCreate() {
        this.simpleImage = Empty.PRESENTED_IMAGE_SIMPLE;
    },
}
</script>


