<template>
    <div class="index">
        <a-spin tip="..." :spinning="spinning" size="large">
            <a-layout>
                <a-layout-header v-if="!this.isZoom">
                    <a-row>
                        <a-col :span="1">
                            <div class="logo"></div>
                        </a-col>
                        <a-col :span="6">
                            <div class="customerInformation">
                                <p>:{{ this.customerName }}</p>
                                <p>:{{ this.projectName }}</p>
                            </div>
                        </a-col>
                        <a-col :span="1">
                            <div
                                v-if="consolidationScopeBut"
                                style="padding-top: 30px; padding-left: 5px"
                            >
                                <span
                                    @click="queryMergeProjectList"
                                    style="
                                        color: rgb(0, 128, 0);
                                        background-color: rgb(239, 239, 239);
                                        font-size: 12px;
                                        padding: 1px 6px;
                                        cursor: pointer;
                                        display: inline-block;
                                        min-width: 75px;
                                    "
                                >
                                    {{ butContent }}
                                </span>
                            </div>
                        </a-col>

                        <a-col :span="2">
                            <div class="index-num">P004-TYH1</div>
                        </a-col>
                        <a-col :span="13">
                            <div class="steps">
                                <a-steps
                                    :current="current"
                                    @change="onChange"
                                    size="small"
                                >
                                    <a-step
                                        :title="item.title"
                                        v-for="item in stepData"
                                        :key="item.index"
                                        @click="stepClick(item)"
                                    />
                                </a-steps>
                            </div>
                        </a-col>
                    </a-row>
                </a-layout-header>
                <a-layout-content>
                    <div class="operation-buttonList">
                        <a-popconfirm
                            title=""
                            ok-text=" "
                            cancel-text=" "
                            @confirm="resetMerchant"
                        >
                            <a-button
                                type="primary"
                                shape="circle"
                                icon="poweroff"
                                style="
                                    background: rgb(253, 86, 34);
                                    border-color: rgb(253, 86, 34);
                                "
                                size="small"
                                title=""
                                v-if="this.current == 0"
                            />
                        </a-popconfirm>
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="plus"
                            size="small"
                            title=""
                            @click="addSubject"
                            v-if="this.current == 0"
                        />
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="plus"
                            size="small"
                            title=""
                            @click="add"
                            v-if="this.current == 1"
                        />
                        <a-popconfirm
                            title=""
                            ok-text=" "
                            cancel-text=" "
                            @confirm="saveBankRangeSelect"
                        >
                            <a-button
                                type="primary"
                                shape="circle"
                                icon="check"
                                size="small"
                                title=""
                                v-if="this.current == 0"
                            />
                        </a-popconfirm>
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="search"
                            @click="searchSampling"
                            size="small"
                            title=""
                            v-if="
                                this.current == 1 ||
                                this.current == 2 ||
                                this.current == 3
                            "
                        />
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="setting"
                            @click="enterpriseMaintenance"
                            v-if="this.current == 2"
                            size="small"
                            title=""
                        />
                        <a-popconfirm
                            title=""
                            ok-text=" "
                            cancel-text=" "
                            @confirm="handlebankList"
                        >
                            <a-button
                                type="primary"
                                shape="circle"
                                icon="link"
                                size="small"
                                title=""
                                v-if="this.current == 1"
                            />
                        </a-popconfirm>
                        <!-- <a-popconfirm
                                title=""
                                ok-text=" "
                                cancel-text=" "
                                @confirm="handleMatchBaiduAddress">
                                <a-button type="primary" shape="circle" icon='environment' size="small" v-if='this.current == 1' title=""/>
                            </a-popconfirm> -->
                        <a-button
                            type="primary"
                            shape="circle"
                            size="small"
                            v-if="this.current == 2"
                            title=""
                            class="iconfont"
                            @click="handleRate"
                            >&#xe7b5;</a-button
                        >
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="check-square"
                            size="small"
                            v-if="this.current == 2"
                            title=""
                            @click="handleRateCalculator"
                        />
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="clock-circle"
                            size="small"
                            v-if="this.current == 2"
                            title=""
                            @click="handleCheckBillState"
                        />
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="fullscreen"
                            v-if="!this.isZoom && current != -1"
                            @click.prevent="zoomOut"
                            size="small"
                            title=""
                        />
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="fullscreen-exit"
                            v-if="this.isZoom && current != -1"
                            @click.prevent="zoomOut"
                            size="small"
                            title=""
                        />
                    </div>
                    <a-tabs
                        v-model="activeTabKey"
                        hide-add
                        type="editable-card"
                        @edit="onEdit"
                        @tabClick="tabClick"
                    >
                        <a-tab-pane
                            v-for="pane in panes"
                            :key="pane.key"
                            :tab="pane.title"
                        >
                        </a-tab-pane>
                    </a-tabs>
                    <bankRangeSelection
                        ref="bankRangeSelection"
                        v-if="current == 0"
                    />
                    <bankList ref="bankList" v-if="current == 1" />
                    <bankBill ref="bankBill" v-if="current == 2" />
                    <analysisOfInterestIncome
                        ref="analysisOfInterestIncome"
                        v-if="current == 3"
                    />
                </a-layout-content>
            </a-layout>
        </a-spin>
        <counter :count="number" @clear="clear" ref="counter" />
        <add ref="add" @refresh="refresh"> </add>
        <consolidation-scope ref="consolidationScope"></consolidation-scope>
    </div>
</template>
<script>
import { setCookie } from '../libs/utils/cookieUtils';
import session from '../data/session/sessionUtils';
import XEUtils from 'xe-utils';
import bankRangeSelection from './bankRangeSelection';
import bankList from './bankList';
import bankBill from './bankBill';
import analysisOfInterestIncome from './analysisOfInterestIncome';
import counter from '../components/counter';
import add from './add';
import consolidationScope from '../components/consolidationScope';

export default {
    name: 'index',
    components: {
        bankRangeSelection,
        bankList,
        bankBill,
        analysisOfInterestIncome,
        counter,
        add,
        consolidationScope,
    },
    computed: {
        nextStep() {
            return this.$store.state.nextStep;
        },
    },
    watch: {
        nextStep: {
            handler(val) {
                if (val) {
                    this.onChange(1);
                }
            },
            immediate: true,
        },
    },
    data() {
        return {
            spinning: false,
            current: -1,
            stepData: [
                {
                    title: '',
                    key: '0',
                    content: 'bankRangeSelection',
                },
                { title: '', key: '1', content: 'bankList' },
                { title: '', key: '2', content: 'bankBill' },
                {
                    title: '',
                    key: '3',
                    content: 'analysisOfInterestIncome',
                },
            ],
            activeTabKey: '0',
            panes: [],
            newTabIndex: 0,
            customerName: null,
            projectName: null,
            isZoom: false,
            customerData: {},
            num: 0,
            number: 0,
            consolidationScopeBut: false,
            butContent: '',
        };
    },
    methods: {
        zoomOut() {
            this.isZoom = !this.isZoom;
        },
        addSubject() {
            //
            this.$refs.bankRangeSelection.openSubjectTreeDialog();
        },
        //
        add() {
            this.$refs.add.showModal();
        },
        resetMerchant() {
            //
            this.$refs.bankRangeSelection.resetBank();
        },
        saveBankRangeSelect() {
            //
            this.$refs.bankRangeSelection.submitBankRangeData();
        },
        getSujectTree() {
            let params = {
                lockCustomerId: this.customerData.curCustomerId,
                lockYyyy: this.customerData.curAccYear,
            };
            this.$sacpApis
                .findAccSubjectType(params)
                .then((res) => {
                    if (res.success) {
                        session.saveBankRangeTree(res.data);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        tabClick(current) {
            this.current = Number(current);
            let selectedTab = this.panes.filter((item) => item.key == current);
        },
        stepClick(item) {
            let hasExist = this.panes.some((item) => item.key == item.key);
            if (!hasExist) this.panes.push(item);
            this.activeTabKey = item.key;
        },
        onChange(current) {
            //
            this.current = current;
            const panes = this.panes;
            let hasExist = this.panes.some(
                (item) => item.key == this.stepData[current].key
            );
            if (!hasExist) this.panes.push(this.stepData[current]);
            this.activeTabKey = this.stepData[current].key;
            if (this.$store.state.nextStep)
                this.$store.commit('setNextStep', false);
        },
        onEdit(targetKey, action) {
            this[action](targetKey);
        },
        remove(targetKey) {
            let activeKey = this.activeTabKey;
            let lastIndex;
            this.panes.forEach((pane, i) => {
                if (pane.key === targetKey) {
                    lastIndex = i - 1;
                }
            });
            const panes = this.panes.filter((pane) => pane.key !== targetKey);
            if (!panes.length) activeKey = '-1'; //tab
            if (panes.length && activeKey === targetKey) {
                if (lastIndex >= 0) {
                    activeKey = panes[lastIndex].key;
                } else {
                    activeKey = panes[0].key;
                }
            }
            this.panes = panes;
            this.activeTabKey = activeKey;
            this.current = Number(activeKey);
        },
        getCustomerInfomation() {
            this.spinning = true;
            this.$sacpApis
                .queryCustomerInformation()
                .then((res) => {
                    if (res.success) {
                        this.customerData = res.data[0];
                        this.customerName = `${this.customerData.curCustomerId}-${this.customerData.curCustomerName}`;
                        this.projectName = this.customerData.curProjectName;
                        session.saveUserInfo(this.customerData);
                        const { curCustomerId, curProjectId } = res.data[0];
                        const param = {
                            lockCustomerId: curCustomerId,
                            lockProjectId: curProjectId,
                        };
                        this.$refs.consolidationScope
                            .queryMergeProjectType(param)
                            .then((res) => {
                                if (!res) {
                                    this.consolidationScopeBut = false;
                                    this.butContent = '';
                                } else {
                                    this.consolidationScopeBut = true;
                                    this.butContent = res;
                                }
                            });
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.getSujectTree();
                    this.spinning = false;
                });
        },
        searchSampling() {
            if (this.current == 1) {
                this.$refs.bankList.openSearchDialog();
            } else if (this.current == 2) {
                this.$refs.bankBill.openSearchDialog();
            } else {
                this.$refs.analysisOfInterestIncome.openSearchDialog();
            }
        },
        enterpriseMaintenance() {
            this.$refs.bankBill.shownEtrpriseMaintenanceDialog();
        },
        handlebankList() {
            if (this.current == 1) {
                this.$refs.bankList.queryBankDataCollection();
            } else {
                this.handleMatchContactsCustomer();
            }
        },
        handleMatchBaiduAddress() {
            this.$refs.bankList.matchBaiduAddress();
        },
        handleRate() {
            //
            this.$refs.bankBill.handleRateByHand();
        },
        handleRateCalculator() {
            //
            this.$refs.bankBill.handleRateCalculator();
        },
        handleCheckBillState() {
            this.$refs.bankBill.handleCheckBillState();
        },
        routerListen() {
            let routeParam = window.location.hash.split('/');
            routeParam = routeParam[routeParam.length - 1];
            routeParam != 'index'
                ? (this.current = Number(
                      this.stepData.filter(
                          (item) => item.content == routeParam
                      )[0].key
                  ))
                : (this.current = this.current);
        },
        queryMergeProjectList() {
            this.$refs.consolidationScope.queryMergeProjectList();
        },
        clear(val) {
            this.number = val;
        },
        refresh() {
            this.$refs.bankList.queryBankListInitData();
        },
    },
    mounted() {
        window.addEventListener('click', (e) => {
            if (this.$refs.counter && this.$refs.counter.icon) {
                if (!e.target.innerText) return;
                const icon = this.$refs.counter.icon;
                const text = e.target.innerText.trim().replaceAll(',', '');
                const star = text.indexOf('.');
                const end = text.lastIndexOf('.');
                if (
                    !isNaN(text * 1) &&
                    star === end &&
                    star > -1 &&
                    end > -1 &&
                    text.length - star === 3 &&
                    icon
                ) {
                    this.number = text * 1;
                }
            }
        });
    },
    created() {
        this.getCustomerInfomation();
        this.routerListen();
        // setCookie(
        //     'SESSION',
        //     'MTZiMDczMmMtOGU2MC00MTI5LThlOTYtZjdmNzY0ZDg2ZmVi',
        //     1
        // );
    },
};
</script>


